import { getRoleIdFromToken, isTokenValid } from './jwt'

/**
 * 
 */

/**
 * token
 * @returns {string|null} token
 */
export function getToken() {
  return localStorage.getItem('token')
}

/**
 * ID
 * @returns {number|null} ID
 */
export function getCurrentUserRoleId() {
  const token = getToken()
  if (!token || !isTokenValid(token)) {
    return null
  }
  return getRoleIdFromToken(token)
}

/**
 * 
 * @returns {boolean} 
 */
export function isAdmin() {
  const roleId = getCurrentUserRoleId()
  return roleId === 0
}

/**
 * 
 * @param {number} targetRoleId ID
 * @returns {boolean} 
 */
export function hasRole(targetRoleId) {
  const roleId = getCurrentUserRoleId()
  return roleId === targetRoleId
}

/**
 * token
 * @returns {boolean} 
 */
export function isLoggedIn() {
  const token = getToken()
  return token && isTokenValid(token)
}

/**
 * 
 * @param {Function} fn 
 * @param {string} errorMsg 
 * @returns {Function} 
 */
export function requireAdmin(fn, errorMsg = '') {
  return function(...args) {
    if (!isAdmin()) {
      // 
      console.warn(errorMsg)
      return false
    }
    return fn.apply(this, args)
  }
} 