<template>
  <div class="speed-limit-container">
    <!--  -->
    <div class="page-header">
      <h1 class="page-title">
        <i class="el-icon-odometer"></i>
        
      </h1>
      <div class="header-actions">
        <!--  -->
        <el-input
          v-model="searchKeyword"
          placeholder=""
          prefix-icon="el-icon-search"
          @keyup.enter.native="handleSearch"
          clearable
          style="width: 250px; margin-right: 15px;"
        >
          <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
        </el-input>
        
        <el-button
          type="primary" 
          icon="el-icon-plus" 
          @click="handleAdd"
          class="add-btn"
        >
          
        </el-button>
        
        <el-button 
          type="success" 
          icon="el-icon-refresh" 
          @click="loadSpeedLimits"
          :loading="loading"
        >
          
        </el-button>
      </div>
    </div>

    <!--  -->
    <div class="table-container">
      <el-table 
        :data="speedLimitList" 
        v-loading="loading"
        stripe
        border
        style="width: 100%"
      >
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column prop="name" label="" show-overflow-tooltip></el-table-column>
        <el-table-column prop="speed" label="" >
          <template slot-scope="scope">
            <el-tag type="info">{{ scope.row.speed }} Mbps</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="status" label="" width="100">
          <template slot-scope="scope">
            <el-tag :type="scope.row.status === 1 ? 'success' : 'danger'">
              {{ scope.row.status === 1 ? '' : '' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="tunnelName" label="" show-overflow-tooltip>
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.tunnelName">{{ scope.row.tunnelName }}</el-tag>
            <span v-else class="text-muted"></span>
          </template>
        </el-table-column>
        <el-table-column prop="createdTime" label="" >
          <template slot-scope="scope">
            {{ scope.row.createdTime | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column prop="updatedTime" label="" >
          <template slot-scope="scope">
            {{ scope.row.updatedTime | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label=""  fixed="right">
          <template slot-scope="scope">
            <el-button 
              size="mini" 
              type="primary" 
              icon="el-icon-edit"
              @click="handleEdit(scope.row)"
            >
            </el-button>
            <el-button 
              size="mini" 
              type="danger" 
              icon="el-icon-delete"
              @click="handleDelete(scope.row)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      
      <!--  -->
      <div v-if="!loading && speedLimitList.length === 0" class="empty-state">
        <el-empty description="">
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-empty>
      </div>
    </div>

    <!-- / -->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible" 
      width="500px"
      @close="resetForm"
    >
      <el-form 
        :model="speedLimitForm" 
        :rules="rules" 
        ref="speedLimitForm"
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input 
            v-model="speedLimitForm.name" 
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        
        <el-form-item label="" prop="speed">
          <el-input-number
            v-model="speedLimitForm.speed"
            :min="1"
            :max="1000000"
            placeholder=""
            style="width: 100%;"
          ></el-input-number>
          <div class="form-hint">
            Mbps
          </div>
        </el-form-item>
        

        
        <el-form-item label="" prop="tunnelId">
          <el-select 
            v-model="speedLimitForm.tunnelId" 
            placeholder=""
            style="width: 100%;"
            filterable
            @change="handleTunnelChange"
          >
            <el-option
              v-for="tunnel in tunnelList"
              :key="tunnel.id"
              :label="tunnel.name"
              :value="tunnel.id"
            ></el-option>
          </el-select>
          <div class="form-hint">
            
          </div>
        </el-form-item>
      </el-form>
      
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button 
          type="primary" 
          @click="handleSubmit"
          :loading="submitLoading"
        >
          {{ submitLoading ? '...' : ' ' }}
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { createSpeedLimit, getSpeedLimitList, updateSpeedLimit, deleteSpeedLimit, getTunnelList } from "@/api";

export default {
  name: "Limit",
  data() {
    return {
      speedLimitList: [],
      tunnelList: [],
      loading: false,
      dialogVisible: false,
      dialogTitle: '',
      isEdit: false,
      submitLoading: false,
      searchKeyword: '',
      
      // 
      speedLimitForm: {
        id: null,
        name: '',
        speed: 100,
        tunnelId: null,
        tunnelName: '',
        status: 1
      },
      
      // 
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
        ],
        speed: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 1, message: '0', trigger: 'blur' }
        ],
        tunnelId: [
          { required: true, message: '', trigger: 'change' }
        ]
      }
    };
  },
  
  mounted() {
    this.loadSpeedLimits();
    this.loadTunnels();
  },
  
  methods: {
    // 
    async loadSpeedLimits() {
      try {
        this.loading = true;
        const res = await getSpeedLimitList();
        if (res.code === 0) {
          this.speedLimitList = res.data || [];
        } else {
          this.$message.error(res.msg || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.loading = false;
      }
    },

    // 
    async loadTunnels() {
      try {
        const res = await getTunnelList();
        if (res.code === 0) {
          this.tunnelList = res.data || [];
        } else {
          console.error(':', res.msg);
        }
      } catch (error) {
        console.error(':', error);
      }
    },
    
    // 
    handleSearch() {
      if (!this.searchKeyword.trim()) {
        this.loadSpeedLimits();
        return;
      }
      
      // 
      this.loading = true;
      setTimeout(() => {
        const filtered = this.speedLimitList.filter(item => 
          item.name.includes(this.searchKeyword)
        );
        this.speedLimitList = filtered;
        this.loading = false;
      }, 300);
    },
    
    // 
    handleTunnelChange(tunnelId) {
      const selectedTunnel = this.tunnelList.find(tunnel => tunnel.id === tunnelId);
      if (selectedTunnel) {
        this.speedLimitForm.tunnelName = selectedTunnel.name;
      } else {
        this.speedLimitForm.tunnelName = '';
      }
    },

    // 
    handleAdd() {
      this.isEdit = false;
      this.dialogTitle = '';
      this.dialogVisible = true;
    },
    
    // 
    handleEdit(row) {
      this.isEdit = true;
      this.dialogTitle = '';
      this.speedLimitForm = { 
        id: row.id,
        name: row.name,
        speed: row.speed,
        tunnelId: row.tunnelId,
        tunnelName: row.tunnelName,
        status: row.status || 1
      };
      this.dialogVisible = true;
    },
    
    // 
    async handleDelete(row) {
      try {
        await this.$confirm(` "${row.name}" `, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        
        const res = await deleteSpeedLimit(row.id);
        if (res.code === 0) {
          this.$message.success('');
          this.loadSpeedLimits();
        } else {
          this.$message.error(res.msg || '');
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error);
          this.$message.error('');
        }
      }
    },
    
    // 
    async handleSubmit() {
      try {
        await this.$refs.speedLimitForm.validate();
        
        this.submitLoading = true;
        let res;
        
        if (this.isEdit) {
          res = await updateSpeedLimit(this.speedLimitForm);
        } else {
          const { id, ...createData } = this.speedLimitForm;
          res = await createSpeedLimit(createData);
        }
        
        if (res.code === 0) {
          this.$message.success(this.isEdit ? '' : '');
          this.dialogVisible = false;
          this.loadSpeedLimits();
        } else {
          this.$message.error(res.msg || '');
        }
      } catch (error) {
        if (error !== false) { // 
          console.error(':', error);
          this.$message.error('');
        }
      } finally {
        this.submitLoading = false;
      }
    },
    
    // 
    resetForm() {
      this.speedLimitForm = {
        id: null,
        name: '',
        speed: 100,
        tunnelId: null,
        tunnelName: '',
        status: 1
      };
      this.$nextTick(() => {
        if (this.$refs.speedLimitForm) {
          this.$refs.speedLimitForm.clearValidate();
        }
      });
    }
  },
  
  filters: {
    dateFormat(value) {
      if (!value) return '';
      const date = new Date(value);
      return date.toLocaleString();
    }
  }
};
</script>

