<template>
  <div class="tunnel-container">
    <!--  -->
    <div class="page-header">
      <h1 class="page-title">
        <i class="el-icon-connection"></i>
        
      </h1>
      <div class="header-actions">
        <el-button 
          type="primary" 
          icon="el-icon-plus" 
          @click="handleAdd"
          class="add-btn"
        >
          
        </el-button>
        
        <el-button 
          type="success" 
          icon="el-icon-refresh" 
          @click="loadTunnels"
          :loading="loading"
        >
          
        </el-button>
      </div>
    </div>

    <!--  -->
    <div class="cards-container" v-loading="loading">
      <div class="cards-grid">
        <div 
          v-for="tunnel in tunnelList" 
          :key="tunnel.id" 
          class="tunnel-card"
          :class="{ 
            'disabled': tunnel.status === 0
          }"
        >
          <!--  -->
          <div class="tunnel-header">
            <h3 class="tunnel-name">{{ tunnel.name }}</h3>
            <div class="tunnel-badges">
              <el-tag v-if="tunnel.type === 1" type="primary" size="mini"></el-tag>
              <el-tag v-else type="warning" size="mini"></el-tag>
              <el-tag v-if="tunnel.flow === 1" type="success" size="mini"></el-tag>
              <el-tag v-else type="info" size="mini"></el-tag>
            </div>
          </div>

          <!--  -->
          <div class="tunnel-flow">
            <!--  -->
            <div class="flow-item client">
              <div class="flow-icon">
                <i class="el-icon-monitor"></i>
              </div>
              <div class="flow-label"></div>
            </div>

            <!--  -->
            <div class="flow-arrow">
              <i class="el-icon-right"></i>
            </div>

            <!--  -->
            <div class="flow-item entrance">
              <div class="flow-icon">
                <i class="el-icon-upload"></i>
              </div>
              <div class="flow-label"></div>
              <div class="flow-detail">
                <div class="detail-item">
                  <span class="label">IP:</span>
                  <span class="value">{{ tunnel.inIp }}</span>
                </div>
                <div class="detail-item">
                  <span class="label">:</span>
                  <span class="value">{{ tunnel.inPortSta === tunnel.inPortEnd ? tunnel.inPortSta : `${tunnel.inPortSta}-${tunnel.inPortEnd}` }}</span>
                </div>
              </div>
            </div>

            <!--  -->
            <div class="flow-arrow">
              <i class="el-icon-right"></i>
            </div>

            <!--  -->
            <div class="flow-item exit">
              <div class="flow-icon">
                <i class="el-icon-download"></i>
              </div>
              <div class="flow-label"></div>
              <div class="flow-detail">
                <div class="detail-item">
                  <span class="label">IP:</span>
                  <span class="value">{{ tunnel.outIp }}</span>
                </div>
                <div class="detail-item">
                  <span class="label">:</span>
                  <span class="value">{{ tunnel.outIpSta === tunnel.outIpEnd ? tunnel.outIpSta : `${tunnel.outIpSta}-${tunnel.outIpEnd}` }}</span>
                </div>
              </div>
            </div>

            <!--  -->
            <div class="flow-arrow">
              <i class="el-icon-right"></i>
            </div>

            <!--  -->
            <div class="flow-item target">
              <div class="flow-icon">
                <i class="el-icon-position"></i>
              </div>
              <div class="flow-label"></div>
              <div class="flow-detail">
                <div class="detail-item">
                  <span class="label"></span>
                </div>
              </div>
            </div>
          </div>

          <!--  -->
          <div class="tunnel-meta">
            <div class="meta-item">
              <i class="el-icon-time"></i>
              <span>{{ tunnel.createdTime | dateFormat }}</span>
            </div>
          </div>

          <!--  -->
          <div class="tunnel-actions">
            <el-button 
              size="small" 
              type="danger" 
              icon="el-icon-delete"
              @click="handleDelete(tunnel)"
            >
              
            </el-button>
          </div>
        </div>
      </div>

      <!--  -->
      <div v-if="!loading && tunnelList.length === 0" class="empty-state">
        <i class="el-icon-connection"></i>
        <p></p>
        <el-button type="primary" @click="handleAdd"></el-button>
      </div>
    </div>

    <!-- / -->
    <el-dialog 
