      :visible.sync="dialogVisible" 
      width="600px"
      @close="resetForm"
    >
      <el-form 
        :model="tunnelForm" 
        :rules="rules" 
        ref="tunnelForm" 
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input 
            v-model="tunnelForm.name" 
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>

        <el-form-item label="" prop="type">
          <el-radio-group v-model="tunnelForm.type" @change="handleTypeChange">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="" prop="flow">
          <el-radio-group v-model="tunnelForm.flow">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2">+</el-radio>
          </el-radio-group>
          <div class="form-hint">
            
          </div>
        </el-form-item>

        <el-divider content-position="left"></el-divider>

        <el-form-item label="" prop="inNodeId">
          <el-select 
            v-model="tunnelForm.inNodeId" 
            placeholder=""
            style="width: 100%"
            @change="handleNodeChange"
          >
            <el-option 
              v-for="node in onlineNodes" 
              :key="node.id" 
              :label="`${node.name} (${node.status === 1 ? '' : ''})`" 
              :value="node.id"
              :disabled="false"
            >
              <span style="float: left">{{ node.name }}</span>
              <span style="float: right; font-size: 13px"
                    :style="{ color: node.status === 1 ? '#67c23a' : '#f56c6c' }">
                {{ node.status === 1 ? '' : '' }}
              </span>
            </el-option>
          </el-select>
        </el-form-item>

        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="" prop="inPortSta">
              <el-input-number 
                v-model="tunnelForm.inPortSta" 
                :min="1" 
                :max="65535"
                placeholder=""
                style="width: 100%"
              ></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="inPortEnd">
              <el-input-number 
                v-model="tunnelForm.inPortEnd" 
                :min="1" 
                :max="65535"
                placeholder=""
                style="width: 100%"
              ></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>

        <!-- (type=2) -->
        <template v-if="tunnelForm.type === 2">
          <el-divider content-position="left"></el-divider>

          <el-form-item label="" prop="outNodeId">
            <el-select 
              v-model="tunnelForm.outNodeId" 
              placeholder=""
              style="width: 100%"
              @change="handleNodeChange"
            >
              <el-option 
                v-for="node in onlineNodes" 
                :key="node.id" 
                :label="`${node.name} (${node.status === 1 ? '' : ''})`" 
                :value="node.id"
                :disabled="tunnelForm.inNodeId && node.id === tunnelForm.inNodeId"
              >
                <span style="float: left">{{ node.name }}</span>
                <span style="float: right; font-size: 13px"
                      :style="{ color: node.status === 1 ? '#67c23a' : '#f56c6c' }">
                  {{ node.status === 1 ? '' : '' }}
                  {{ tunnelForm.inNodeId && node.id === tunnelForm.inNodeId ? ' ()' : '' }}
                </span>
              </el-option>
            </el-select>
          </el-form-item>

          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="outIpSta">
                <el-input-number 
                  v-model="tunnelForm.outIpSta" 
                  :min="10000"
                  :max="65535"
                  placeholder=""
                  style="width: 100%"
                ></el-input-number>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="outIpEnd">
                <el-input-number 
                  v-model="tunnelForm.outIpEnd" 
                  :min="10000"
                  :max="65535"
                  placeholder=""
                  style="width: 100%"
                ></el-input-number>
              </el-form-item>
            </el-col>
          </el-row>
