<script>
import { createTunnel, deleteTunnel, getTunnelList, getNodeList } from "@/api";

export default {
  name: "Tunnel",
  data() {
    return {
      tunnelList: [],
      nodeList: [],
      loading: false,
      dialogVisible: false,
      dialogTitle: '',
      submitLoading: false,
      tunnelForm: {
        id: null,
        name: '',
        inNodeId: null,
        inPortSta: 10000,
        inPortEnd: 65535,
        outNodeId: null,
        outIpSta: 10000,
        outIpEnd: 65535,
        type: 1,
        flow: 1,  // 
        status: 1  // 
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, message: '2', trigger: 'blur' },
          { max: 50, message: '50', trigger: 'blur' }
        ],
        inNodeId: [
          { required: true, message: '', trigger: 'change' }
        ],
        inPortSta: [
          { required: true, message: '', trigger: 'blur' }
        ],
        inPortEnd: [
          { required: true, message: '', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'change' }
        ],
        flow: [
          { required: true, message: '', trigger: 'change' }
        ],
        // 
        outNodeId: [
          { required: false, message: '', trigger: 'change' }
        ],
        outIpSta: [
          { required: false, message: '', trigger: 'blur' }
        ],
        outIpEnd: [
          { required: false, message: '', trigger: 'blur' }
        ]
      }
    };
  },

  filters: {
    dateFormat(timestamp) {
      if (!timestamp) return '-';
      const date = new Date(timestamp);
      return date.toLocaleString('zh-CN');
    }
  },

  computed: {
    // 
    onlineNodes() {
      return this.nodeList || [];
    }
  },

  mounted() {
    this.loadTunnels();
  },

  methods: {
    // 
    loadNodes() {
      getNodeList().then(res => {
        if (res.code === 0) {
          this.nodeList = res.data || [];
        } else {
          this.$message.error(res.msg || '');
        }
      }).catch(error => {
        this.$message.error('');
      });
    },

    // 
    handleNodeChange() {
      // 
      // 
    },

    // 
    loadTunnels() {
      this.loading = true;

      getTunnelList().then(res => {
        this.loading = false;
        if (res.code === 0) {
          this.tunnelList = res.data || [];
        } else {
          this.$message.error(res.msg || '');
        }
      }).catch(error => {
        this.loading = false;
        this.$message.error('');
      });
    },

    // 
    handleAdd() {
      this.dialogVisible = true;
      this.resetForm();
      this.loadNodes(); // 
    },

    //  - 

    // 
    handleDelete(tunnel) {
      this.$confirm(` "${tunnel.name}" `, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteTunnel(tunnel.id).then(res => {
          if (res.code === 0) {
            this.$message.success('');
            this.loadTunnels();
          } else {
            this.$message.error(res.msg || '');
          }
        });
      }).catch(() => {
        // 
      });
    },

    //  - 

    // 
    handleTypeChange(value) {
      if (value === 2) {
        // 
        this.rules.outNodeId[0].required = true;
        this.rules.outIpSta[0].required = true;
        this.rules.outIpEnd[0].required = true;
      } else {
        // 
        this.rules.outNodeId[0].required = false;
        this.rules.outIpSta[0].required = false;
        this.rules.outIpEnd[0].required = false;
        
        // 
        this.tunnelForm.outNodeId = null;
        this.tunnelForm.outIpSta = null;
        this.tunnelForm.outIpEnd = null;
      }
    },

    // 
    handleSubmit() {
      this.$refs.tunnelForm.validate(valid => {
        if (valid) {
          // 
          if (this.tunnelForm.inPortSta > this.tunnelForm.inPortEnd) {
            this.$message.error('');
            return;
          }

          // 
          if (this.tunnelForm.type === 2 && 
              this.tunnelForm.inNodeId === this.tunnelForm.outNodeId) {
            this.$message.error('');
            return;
          }

          if (this.tunnelForm.type === 2 && 
              this.tunnelForm.outIpSta && this.tunnelForm.outIpEnd &&
              this.tunnelForm.outIpSta > this.tunnelForm.outIpEnd) {
            this.$message.error('');
            return;
          }

          this.submitLoading = true;
          
          // 
          const data = { ...this.tunnelForm };
          
          createTunnel(data).then(res => {
            this.submitLoading = false;
            if (res.code === 0) {
              this.$message.success('');
              this.dialogVisible = false;
              this.loadTunnels();
            } else {
              this.$message.error(res.msg || '');
            }
          }).catch(() => {
            this.submitLoading = false;
            this.$message.error('');
          });
        }
      });
    },

    // 
    resetForm() {
      this.tunnelForm = {
        id: null,
        name: '',
        inNodeId: null,
        inPortSta: 10000,
        inPortEnd: 65535,
        outNodeId: null,
        outIpSta: 10000,
        outIpEnd: 65535,
        type: 1,
        flow: 1,  // 
        status: 1  // 
      };
      if (this.$refs.tunnelForm) {
        this.$refs.tunnelForm.clearValidate();
      }
    }
  }
};
</script>

