<template>
  <div class="forward-container">
    <!--  -->
    <div class="page-header">
      <h1 class="page-title"></h1>
      <div class="header-actions">
        <!--  -->
        <el-input
          v-model="searchKeyword"
          placeholder=""
          prefix-icon="el-icon-search"
          @keyup.enter.native="handleSearch"
          clearable
          style="width: 250px; margin-right: 15px;"
        >
          <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
        </el-input>
        
        <el-button 
          type="primary" 
          icon="el-icon-plus" 
          @click="handleAdd"
          class="add-btn"
        >
          
        </el-button>
      </div>
    </div>

    <!--  -->
    <div class="table-container">
      <div v-loading="loading" class="cards-container">
        <div v-if="forwardList.length === 0 && !loading" class="empty-state">
          <el-empty description=""></el-empty>
        </div>
        
        <div 
          v-for="forward in forwardList" 
          :key="forward.id" 
          class="forward-card"
        >
          <div class="card-header">
            <div class="card-title">
              <i class="el-icon-connection"></i>
              {{ forward.name }}
            </div>
            <div class="card-actions">
              <!--  -->
              <el-switch
                v-model="forward.serviceRunning"
                :loading="forward.switchLoading"
                :disabled="forward.status !== 1 && forward.status !== 0"
                active-color="#13ce66"
                inactive-color="#ff4949"
                @change="handleServiceToggle(forward)"
                style="margin-right: 10px;"
              ></el-switch>
              
              <el-button 
                size="mini" 
                type="primary" 
                icon="el-icon-edit"
                @click="handleEdit(forward)"
                circle
              ></el-button>
              <el-button 
                size="mini" 
                type="danger" 
                icon="el-icon-delete"
                @click="handleDelete(forward)"
                circle
              ></el-button>
            </div>
          </div>
          
          <div class="card-body">
            <div class="info-row">
              <div class="info-item">
                <span class="label">:</span>
                <span class="value">{{ forward.tunnelName || '' }}</span>
              </div>
              <div class="info-item">
                <span class="label">:</span>
                <el-tag 
                  :type="getStatusType(forward.status)"
                  size="small"
                >
                  {{ getStatusText(forward.status) }}
                </el-tag>
              </div>
            </div>
            
            <div class="info-row">
              <div class="info-item">
                <span class="label">:</span>
                <span class="value">{{ forward.inIp}}:{{ forward.inPort }}</span>
              </div>
              <div class="info-item">
                <span class="label">:</span>
                <span class="value">{{ forward.remoteAddr}}</span>
              </div>
            </div>
            
            <div class="info-row">
              <div class="info-item full-width">
                <span class="label">:</span>
                <span class="value">{{ forward.createdTime | dateFormat }}</span>
              </div>
              <div class="info-item full-width" v-admin>
                <span class="label">:</span>
                <span class="value">{{ forward.userName}}</span>
              </div>
            </div>
            
            <div class="info-row">
              <div class="info-item">
                <span class="label">:</span>
                <div class="flow-stats-mini">
                  <div class="flow-stat-item">
                    <span class="flow-stat-label">:</span>
                    <span class="flow-stat-value in">{{ formatFlow(forward.inFlow || 0) }}</span>
                  </div>
                  <div class="flow-stat-item">
                    <span class="flow-stat-label">:</span>
                    <span class="flow-stat-value out">{{ formatFlow(forward.outFlow || 0) }}</span>
                  </div>
                  <div class="flow-stat-item">
                    <span class="flow-stat-label">:</span>
                    <span class="flow-stat-value total">{{ formatFlow((forward.inFlow || 0) + (forward.outFlow || 0)) }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- / -->
    <el-dialog 
      :title="dialogTitle" 
      :visible.sync="dialogVisible" 
      width="600px"
      @close="resetForm"
    >
      <el-form 
        :model="forwardForm" 
        :rules="rules" 
        ref="forwardForm" 
        label-width="100px"
      >
        <el-form-item label="" prop="name">
          <el-input 
            v-model="forwardForm.name" 
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        
        <el-form-item label="" prop="tunnelId">
          <el-select 
            v-model="forwardForm.tunnelId" 
            placeholder=""
            style="width: 100%;"
            @change="handleTunnelChange"
          >
            <el-option
              v-for="tunnel in tunnelList"
              :key="tunnel.id || tunnel.tunnelId"
              :label="getTunnelDisplayName(tunnel)"
              :value="tunnel.id || tunnel.tunnelId"
            ></el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="" prop="remoteAddr">
          <el-input 
            v-model="forwardForm.remoteAddr" 
            placeholder=": 192.168.1.100:8080"
            clearable
          >
            <template slot="prepend"></template>
          </el-input>
          <div class="form-hint">
            : IP:  :
          </div>
        </el-form-item>
        

      </el-form>
      
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button 
          type="primary" 
          @click="handleSubmit"
          :loading="submitLoading"
        >
          {{ submitLoading ? '...' : ' ' }}
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { 
  createForward, 
  getForwardList, 
  updateForward, 
  deleteForward, 
  userTunnel, 
  getTunnelList,
  pauseForwardService,
  resumeForwardService
} from "@/api";

export default {
  name: "Forward",
  data() {
    return {
      loading: false,
      submitLoading: false,
      searchKeyword: '',
      forwardList: [],
      tunnelList: [],
      selectedTunnel: null,
      
      // 
      dialogVisible: false,
      dialogTitle: '',
      isEdit: false,
      
      // 
      forwardForm: {
        id: null,
        userId: null,
        name: '',
        tunnelId: null,
        remoteAddr: ''
      },
      
      // 
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
        ],
        tunnelId: [
          { required: true, message: '', trigger: 'change' }
        ],
        remoteAddr: [
          { required: true, message: '', trigger: 'blur' },
          { 
            pattern: /^[\w\.-]+:\d+$/, 
            message: ': 192.168.1.100:8080', 
            trigger: 'blur' 
          }
        ]
      }
    }
  },
  
  created() {
    this.loadForwardList();
  },
  
  methods: {
    // 
    async loadForwardList() {
      try {
        this.loading = true;
        const res = await getForwardList();
        if (res.code === 0) {
          // 
          this.forwardList = res.data.map(forward => ({
            ...forward,
            serviceRunning: forward.status === 1, //  status 
            switchLoading: false // 
          }));
        } else {
          this.$message.error(res.msg || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
      } finally {
        this.loading = false;
      }
    },
    
    // 
    async loadTunnelList(callback, forceReload = false) {
      try {
        // 
        if (this.tunnelList.length > 0 && !forceReload) {
          if (callback && typeof callback === 'function') {
            callback();
          }
          return Promise.resolve();
        }

        // 
        let res = await userTunnel();
        if (res.code === 0 && res.data && res.data.length > 0) {
          // 
          this.tunnelList = res.data;
          // 
          if (callback && typeof callback === 'function') {
            callback();
          }
          return Promise.resolve();
        } else {
          throw new Error(res.msg || '');
        }
      } catch (error) {
        console.error(':', error);
        this.$message.error('');
        return Promise.reject(error);
      }
    },
    
    // 
    handleSearch() {
      if (!this.searchKeyword.trim()) {
        this.loadForwardList();
        return;
      }
      
      // 
      this.loading = true;
      setTimeout(() => {
        const filtered = this.forwardList.filter(forward => 
          forward.name.includes(this.searchKeyword) || 
          forward.remoteAddr.includes(this.searchKeyword)
        );
        this.forwardList = filtered;
        this.loading = false;
      }, 300);
    },
    
    // 
    async handleAdd() {
      this.isEdit = false;
      this.dialogTitle = '';
      
      try {
        // 
        await this.loadTunnelList();
        this.dialogVisible = true;
      } catch (error) {
        this.$message.error('');
      }
    },
    
    // 
    async handleEdit(row) {
      this.isEdit = true;
      this.dialogTitle = '';
      
      try {
        // 
        await this.loadTunnelList();
        
        // 
      this.forwardForm = { 
        ...row,
        userId: row.userId // userId
      };
      this.handleTunnelChange(row.tunnelId);
      this.dialogVisible = true;
      } catch (error) {
        this.$message.error('');
      }
    },
    
    // 
    async handleDelete(row) {
      try {
        await this.$confirm(` "${row.name}" `, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        });
        
        const res = await deleteForward(row.id);
        if (res.code === 0) {
          this.$message.success('');
          this.loadForwardList();
        } else {
          this.$message.error(res.msg || '');
        }
      } catch (error) {
        if (error !== 'cancel') {
          console.error(':', error);
          this.$message.error('');
        }
      }
    },
    
    // 
    handleTunnelChange(tunnelId) {
      this.selectedTunnel = this.tunnelList.find(tunnel => 
        tunnel.id === tunnelId || tunnel.tunnelId === tunnelId
      ) || null;
    },
    
    // 
    async handleSubmit() {
      try {
        await this.$refs.forwardForm.validate();
        
        this.submitLoading = true;
        let res;
        
        if (this.isEdit) {
          // 
          const updateData = {
            id: this.forwardForm.id,
            userId: this.forwardForm.userId,
            name: this.forwardForm.name,
            tunnelId: this.forwardForm.tunnelId,
            remoteAddr: this.forwardForm.remoteAddr
          };
          res = await updateForward(updateData);
        } else {
          // iduserId
          const createData = {
            name: this.forwardForm.name,
            tunnelId: this.forwardForm.tunnelId,
            remoteAddr: this.forwardForm.remoteAddr
          };
          res = await createForward(createData);
        }
        
        if (res.code === 0) {
          this.$message.success(this.isEdit ? '' : '');
          this.dialogVisible = false;
          await this.loadForwardList();
        } else {
          this.$message.error(res.msg || '');
        }
      } catch (error) {
        if (error !== false) { // 
          console.error(':', error);
          this.$message.error('');
        }
      } finally {
        this.submitLoading = false;
      }
    },
    
    // 
    resetForm() {
      this.forwardForm = {
        id: null,
        userId: null,
        name: '',
        tunnelId: null,
        remoteAddr: ''
      };
      this.selectedTunnel = null;
      this.$nextTick(() => {
        if (this.$refs.forwardForm) {
          this.$refs.forwardForm.clearValidate();
        }
      });
    },
    
    // 
    getTunnelDisplayName(tunnel) {
      if (!tunnel) return '';
      
      // 
      if (tunnel.tunnelId) {
        const tunnelInfo = this.tunnelList.find(t => t.id === tunnel.tunnelId);
        if (tunnelInfo && tunnelInfo.ip && tunnelInfo.port) {
          return `${tunnelInfo.name || tunnel.tunnelId} (${tunnelInfo.ip}:${tunnelInfo.port})`;
        }
        return `ID: ${tunnel.tunnelId}`;
      }
      
      // 
      if (tunnel.name) {
        if (tunnel.ip && tunnel.port) {
          return `${tunnel.name} (${tunnel.ip}:${tunnel.port})`;
        }
        return tunnel.name;
      }
      
      return `ID: ${tunnel.id || 'unknown'}`;
    },
    
    formatFlow(value) {
      if (value === 0) return '0';
      if (value < 1024) return value + ' B';
      if (value < 1024 * 1024) return (value / 1024).toFixed(2) + ' KB';
      if (value < 1024 * 1024 * 1024) return (value / (1024 * 1024)).toFixed(2) + ' MB';
      return (value / (1024 * 1024 * 1024)).toFixed(2) + ' GB';
    },
    
    // 
    getStatusType(status) {
      switch (parseInt(status)) {
        case 1:
          return 'success'; //  - 
        case 0:
          return 'danger';  //  - 
        case -1:
          return 'danger';  //  - 
        default:
          return 'info';    //  - 
      }
    },
    
    // 
    getStatusText(status) {
      switch (parseInt(status)) {
        case 1:
          return '';
        case 0:
          return '';
        case -1:
          return '';

        default:
          return '';
      }
    },
    



     
     // 
      async handleServiceToggle(forward) {
        // 10
        if (forward.status !== 1 && forward.status !== 0) {
          this.$message.warning('');
          // 
          this.$set(forward, 'serviceRunning', !forward.serviceRunning);
          return;
        }
        
        const targetState = forward.serviceRunning;
       
       try {
         // 
         this.$set(forward, 'switchLoading', true);
         
         let res;
         if (targetState) {
           // 
           res = await resumeForwardService(forward.id);
         } else {
           // 
           res = await pauseForwardService(forward.id);
         }
         
         if (res.code === 0) {
           this.$message.success(targetState ? '' : '');
           // 
           forward.status = targetState ? 1 : 0;
         } else {
           // 
           this.$set(forward, 'serviceRunning', !targetState);
           this.$message.error(res.msg || '');
         }
       } catch (error) {
         console.error(':', error);
         // 
         this.$set(forward, 'serviceRunning', !targetState);
         this.$message.error('');
       } finally {
         // 
         this.$set(forward, 'switchLoading', false);
       }
     }
  },
  
  filters: {
    dateFormat(value) {
      if (!value) return '';
      const date = new Date(value);
      return date.toLocaleString();
    }
  }
}
</script>

