<template>
  <el-container>
    <el-header style="height: 50px;">
      <span style="line-height: 50px"></span>
      <div class="header-avatar">
        <el-avatar style="margin-right: 10px" size="medium" src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"></el-avatar>
        <el-dropdown>
						<span class="el-dropdown-link"  style="color: white">{{name}}</span>

          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="showPasswordDialog">
              <i class="el-icon-key"></i>
              
            </el-dropdown-item>
            <el-dropdown-item @click.native="logout">
              <i class="el-icon-switch-button"></i>
              
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </el-header>

    <el-container>
      <el-aside width="200px">
        <el-menu
            :default-active="path"
            class="el-menu-vertical-demo">

          <router-link to="/index">
            <el-menu-item index="/index" @click="save('/index')">
              <template slot="title">
                <i class="el-icon-box"></i>
                <span slot="title"></span>
              </template>
            </el-menu-item>
          </router-link>

          <router-link to="/limit" v-admin>
            <el-menu-item index="/limit" @click="save('/limit')">
              <template slot="title">
                <i class="el-icon-box"></i>
                <span slot="title"></span>
              </template>
            </el-menu-item>
          </router-link>

          <router-link to="/forward">
          <el-menu-item index="/forward" @click="save('/forward')">
            <template slot="title">
              <i class="el-icon-postcard"></i>
              <span slot="title"></span>
            </template>
          </el-menu-item>
        </router-link>

          <router-link to="/tunnel" v-admin>
            <el-menu-item index="/tunnel" @click="save('/tunnel')">
              <template slot="title">
                <i class="el-icon-postcard"></i>
                <span slot="title"></span>
              </template>
            </el-menu-item>
          </router-link>

          <router-link to="/node" v-admin>
            <el-menu-item index="/node" @click="save('/node')">
              <template slot="title">
                <i class="el-icon-postcard"></i>
                <span slot="title"></span>
              </template>
            </el-menu-item>
          </router-link>

          <router-link to="/user" v-admin>
            <el-menu-item index="/user" @click="save('/user')">
              <template slot="title">
                <i class="el-icon-user"></i>
                <span slot="title"></span>
              </template>
            </el-menu-item>
          </router-link>

        </el-menu>
      </el-aside>
      <el-main>
        <div style="margin: 15px 15px;">
          <router-view/>

          <div style="background-color: white; height: 50px; position: absolute; bottom: 0;left: 199px;right: 0;line-height: 50px; padding-left: 30px;font-size: 12px;color:#58666e;">
             2023 Copyright.
          </div>
        </div>
      </el-main>
    </el-container>

    <!--  -->
    <el-dialog title="" :visible.sync="passwordDialogVisible" width="400px" :before-close="handlePasswordDialogClose">
      <el-form :model="passwordForm" :rules="passwordRules" ref="passwordForm" label-width="100px">
        <el-form-item label="" prop="currentPassword">
          <el-input v-model="passwordForm.currentPassword" type="password" placeholder="" show-password clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPassword">
          <el-input v-model="passwordForm.newPassword" type="password" placeholder="" show-password clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input v-model="passwordForm.confirmPassword" type="password" placeholder="" show-password clearable></el-input>
        </el-form-item>
      </el-form>
      
      <div slot="footer" class="dialog-footer">
        <el-button @click="handlePasswordDialogClose"> </el-button>
        <el-button type="primary" @click="submitPasswordChange" :loading="passwordLoading"> </el-button>
      </div>
    </el-dialog>

  </el-container>
</template>

<script>
import { updatePassword } from '@/api'

export default {
  name: "home",
  data(){
    return{
      path:'',
      name: '',
      // 
      passwordDialogVisible: false,
      passwordLoading: false,
      passwordForm: {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      },
      passwordRules: {
        currentPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, message: '', trigger: 'blur' }
        ],
        newPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: this.validateConfirmPassword, trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.path = localStorage.getItem('e')
    this.name = localStorage.getItem("name")
  },
  methods:{
    save(e){
      localStorage.setItem('e',e)
      this.path = e
    },
    logout(){
      localStorage.clear()
      this.$router.push("/login")
    },
    // 
    showPasswordDialog() {
      this.passwordDialogVisible = true
    },
    // 
    handlePasswordDialogClose() {
      this.passwordDialogVisible = false
      this.resetPasswordForm()
    },
    // 
    resetPasswordForm() {
      this.passwordForm = {
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      }
      if (this.$refs.passwordForm) {
        this.$refs.passwordForm.clearValidate()
      }
    },
    // 
    validateConfirmPassword(rule, value, callback) {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.passwordForm.newPassword) {
        callback(new Error(''))
      } else {
        callback()
      }
    },
    // 
    async submitPasswordChange() {
      try {
        // 
        await this.$refs.passwordForm.validate()
        
        this.passwordLoading = true
        
        // 
        const response = await updatePassword(this.passwordForm)
        
        if (response.code === 0) {
          this.$message.success(response.msg || '')
          this.handlePasswordDialogClose()
          
          // 
          this.$confirm('', '', {
            confirmButtonText: '',
            type: 'success',
            showCancelButton: false
          }).then(() => {
            this.logout()
          }).catch(() => {
            this.logout()
          })
        } else {
          this.$message.error(response.msg || '')
        }
      } catch (error) {
        if (error !== 'validate') {
          this.$message.error('')
          console.error(':', error)
        }
      } finally {
        this.passwordLoading = false
      }
    }
  }
}
</script>

